    conts['UAC']['memberAdd'] = 0x00; // 添加成员
    conts['UAC']['memberChangeRole'] = 0x01; // 修改角色
    conts['UAC']['memberRemove'] = 0x02; // 删除成员

    // 角色操作 0x04 - 0x07
    conts['UAC']['roleAdd'] = 0x04; // 添加角色
