    conts['UAC']['wikiDelete'] = 0x25; // 删除 Wiki
    conts['UAC']['wikiAttachmentChange'] = 0x26; // 添加／编辑附件 
    conts['UAC']['wikiAttachmentDelete'] = 0x27; // 删除附件

