    conts['UAC']['issueUpdateText'] = 0x31; // 编辑问题 标题／描述
    conts['UAC']['issueUpdateMember'] = 0x32; // 编辑问题 指派／关注
    conts['UAC']['issueUpdateDeadline'] = 0x33; // 编辑问题 结束时间
    conts['UAC']['issueUpdateOption'] = 0x34; // 编辑问题 类型／模块／状态／优先级／版本
