    conts['tracupbotTypeDingding'] = 1;
    conts['tracupbotTypeSlack'] = 2;

    // 通知类型
    conts['notificationTypeDM'] = 3;
    conts['notificationTypePublic'] = 1;
    conts['notificationTypeUser'] = 2;

   return conts;
